<template>
  <div class="bg-gray-100" style="height: 100vh">
    <div
      v-if="modal"
      style="
        position: fixed;
        z-index: 100;
        width: 100vw;
        height: 100vh;
        background: black;
      "
    ></div>
    <div class="container">
      <div
        v-if="modal"
        style="
          position: fixed;
          z-index: 101;
          width: 90vw;
          height: 90vh;
          background: white;
        "
        class="mt-3 ml-1 rounded-lg shadow"
      >
        <div class="p-3">
          <br>
          <img
            src="@/static/logo.png"
            style="height: 50px"
            alt=""
            class="my-2"
          />
          <br>
          <p class="font-bold text-lg">
            Ijinkan Aplikasi 
          </p>
          <div class="row text-sm">
            <div class="col-3 mt-3 text-center">
              <span class="text-3xl typcn typcn-camera"></span>
            </div>
            <div class="col-9 px-3 mt-3 text-left">
              Mengambil Photo dan mengidentifikasi atau recognize data wajah
              karyawan
            </div>
            <div class="col-3 mt-3 text-center">
              <span class="text-3xl typcn typcn-location"></span>
            </div>
            <div class="col-9 px-3 mt-3 text-left">
              Mentrack dan mengambil posisi GPS anda untuk mengambil data posisi
              karyawan
            </div>
            <div class="col-12 px-3 text-sm">
              <p>
                Untuk penggunaan functional aplikasi <span @click="modal=false">kami</span> membutuhkan
                pengambilan data photo dan gps untuk melakukan absen karyawan di
                aplikasi ini.
              </p>
              <p class="my-3">
                Pelajari lebih lengkap kebutuhan permission yang digunakan
                aplikasi ini di sini .
                <a
                  href="http://e-bkm.hitdigital.id/privacy"
                  class="font-bold"
                  target="__blank"
                  >Privacy and Policy</a
                >
              </p>
              <div class="row">
                <div class="col-6">
                  <button
                    type="button"
                    @click="dontallow"
                    class="btn btn-sm btn-style2 btn-block text-xs"
                  >
                    Tolak <br />
                    (Don't Allow)
                  </button>
                </div>
                <div class="col-6">
                  <button
                    type="button"
                    @click="allow"
                    class="btn btn-sm btn-primary btn-block text-xs"
                  >
                    Terima <br />
                    (Allow)
                  </button>
                </div>
              </div>
            </div>
          </div>
          <p class="font-semibold p-3 text-sm"></p>
        </div>
      </div>
    </div>
    <div v-if="loading" class="bg-black">
        <div style="height:100vh;" class="d-flex justify-content-center align-items-center shadow-lg rounded-lg">
            <div class="row">
                <div class="col-12">
                    <img src="@/static/face-loading.gif" alt="">
                </div>
                <div class="col-12">
                    <div style="width:100%" class="text-center text-white font-semibold text-lg animate__animated animate__bounce animate__infinite">
                    Login Loading ...
                    </div>
                </div>
            </div>
        </div>
    </div>
    <div v-else
      style="height: 90vh"
      class="d-flex justify-content-center align-items-center rounded-lg animate__animated animate__fadeIn"
    >
      <div style="width: 100%">
        <div class="p-3">
          <img
            style="display: block; margin: auto; width: 30vw"
            src="@/static/logo.png"
            @click="$store.state.group_login=!$store.state.group_login"
            alt=""
          />
          <p class="text-center text-xl p-2 font-semibold" :class="$store.state.group_login?'text-blue-500':''">Welcome</p>
          <p class="text-lg font-semibold text-center text-blue-800">
            Face Recognition Attendance App
          </p>
          <br />
          <br />
          <form action="" @submit.prevent="login">
            <div class="sm-form">
              <input
                type="text"
                required
                id="username"
                name="username"
                class="form-control p-3"
                placeholder="Username ..."
                v-model="vdata['username']"
              />
            </div>
            <div class="sm-form mt-2">
              <input
                type="password"
                required
                id="password"
                name="password"
                class="form-control p-3"
                placeholder="Password ..."
                v-model="vdata['password']"
              />
            </div>
            <div class="text-center">
              <button
                type="submit"
                class="mt-2 btn btn-sm btn-warning p-3 rounded-lg font-semibold btn-block text-lg"
              >
                <i class="fa fa-lock mr-2" aria-hidden="true"></i> Login
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import autophp from "@/plugins/auto";
let sdb = new autophp("ci");
var watchId;
var Datastore = require("nedb");
window.checktimes = new Datastore({
  filename: "checktimes.db", // nama table
  autoload: true
});
window.employees= new Datastore({
  filename: "employees.db", // nama table
  autoload: true
});
export default {
  data() {
    return {
      modal: false,
      vdata: {},
      loading:true,
    };
  },
  methods: {
    async login() {
      let that=this;
      window.employees.remove({},{multi:true},(err,docs)=>{});
      if (!this.vdata.username && !this.vdata.password) {
        if (
          localStorage.getItem("username") &&
          localStorage.getItem("storage-db-id")
        ) {
          this.vdata.username = localStorage.getItem("username");
          this.vdata.password = sdb.scramble(
            localStorage.getItem("storage-db-id")
          );
        }
      }
      if (!this.vdata.username && !this.vdata.password) {
        this.loading=false
        return;
      }
      function appendLocation(location, verb) {}
      if ("geolocation" in navigator) {
        navigator.geolocation.getCurrentPosition(function (location) {
          appendLocation(location, "fetched");
        });
        watchId = navigator.geolocation.watchPosition(appendLocation);
      } else {
        sdb.alert("Geolocation API not supported.");
      }
      try{
          let res = await axios.get(
            this.$store.state.baseURL +
              "/api/hit/login?username=" +
              this.vdata.username +
              "&password=" +
              this.vdata.password
          );
          if (res.data == false) {
            alert("Username atau Password salah !");
          }
          let res2;
          if(that.$store.state.group_login){
            res2 = await axios.get(this.$store.state.baseURL + "/api/employee/group?hit_id=" + res.data);
          }else{
            res2 = await axios.get(this.$store.state.baseURL + "/api/employee/data?hit_id=" + res.data);
          }
          this.$store.state.hit_id = res.data;
          this.$store.state.origin_id= res.data;
          this.$store.state.employees = res2.data;
        //   localStorage.setItem('employees',JSON.stringify(res2.data))
          window.employees.insert(res2.data,(err,docs)=>{})
          localStorage.setItem('hit_id',res.data)
          // localStorage.setItem('employees',JSON.stringify(res2.data))
          localStorage.setItem("username", this.vdata.username);
          localStorage.setItem("storage-db-id", sdb.scramble(this.vdata.password));
          this.$router.push("/loadingInit");
      }catch(err){
        if(localStorage.getItem('hit_id') && localStorage.getItem('hit_id')!=false){
            that.auto_login()
        }else{
          sdb.alert('Gagal Login!')
        }
      }
    },
    auto_login(){
        let that=this;
          window.employees.find({},async (err,docs)=>{
            console.log('docs',docs)
              if(docs && docs.length>0){
                // console.log('docs',docs)
                    if(localStorage.getItem('hit_id')){
                      let hit_id=localStorage.getItem('hit_id')
                      that.$store.state.employees=docs
                      that.$store.state.employee=docs.filter(e=>e.hit_id==hit_id)[0]
                      that.$store.state.hit_id=hit_id
                      that.$router.push('/loadingInit')
                    }
                }else{
                  that.loading=false
                }
        })
        this.loading=false;
    },
    dontallow() {
      if (typeof cordova == "object") {
        document.addEventListener("deviceready", function () {
          navigator.app.exitApp();
        });
      }
    },
    allow() {
      let that = this;
      if (typeof cordova == "object") {
        sdb.loadingOn();
        document.addEventListener("deviceready", function () {
          var permissions = cordova.plugins.permissions;
          var list = [
            permissions.CAMERA,
            permissions.ACCESS_BACKGROUND_LOCATION,
            permissions.ACCESS_FINE_LOCATION,
            permissions.BLUETOOTH,
            permissions.BLUETOOTH_ADMIN,
            permissions.BLUETOOTH_SCAN,
            permissions.BLUETOOTH_ADVERTISE,
            permissions.BLUETOOTH_CONNECT,
          ];

          permissions.hasPermission(list, success, null);

          function error() {
            console.warn("Camera or Accounts permission is not turned on");
          }

          function success(status) {
            if (!status.hasPermission) {
              permissions.requestPermissions(
                list,
                function (status) {
                //   that.getGPS();
                  if (!status.hasPermission) error();
                },
                error
              );
            }
          }

          //
          localStorage.setItem("permission", "true");
          that.modal = false;
          sdb.loadingOff();
          that.$forceUpdate();
        });
      }else{
        that.modal=false
        localStorage.setItem("permission", "true");
      }
    },
  },
  layout:'app',
  mounted() {
    let that=this;
    this.auto_login()
    if(localStorage.getItem('permission')){
      if(localStorage.getItem('permission')=='false'){
        
        this.modal=true
      }else{
        //  that.getGPS();
      }
    }else{
      localStorage.setItem('permission','false')
      this.modal=true
    }
    //  if( /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent) ) {
    //     // some code..
    //     }else{
    //     this.$router.push('/404')
    // }
  },
};
</script>